#nav-controls {
  display: flex;
  justify-content: space-between;
  flex-direction: row-reverse;
  flex-wrap: wrap;
}

#prevstop {
  display: flex;
  flex-grow: 1;
  margin-right: 1em;
}

#nextonly {
  display: block;
  text-align: right;
}

#navcontrols > .a-button-next {
  order: 1;
}

#navcontrols > .a-button-prev {
  order: 3;
}

#navcontrols > .a-button-stop {
  order: 2;
}

.page-links {
  margin-top: 1em;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
}
.page-links a {
  color: #212C4C;
  display: inline-block;
  margin-right: 1em;
}